import {
  Component,
  OnInit,
  Input,
  ElementRef,
  HostBinding,
  OnChanges,
  SimpleChanges,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  OnDestroy
} from '@angular/core';

import { select as d3_select, mouse as d3_mouse, event as d3_event } from 'd3-selection';
import { scaleOrdinal as d3_scaleOrdinal } from 'd3-scale';
import { pie as d3_pie, arc as d3_arc } from 'd3-shape';
import { interpolate as d3_interpolate } from 'd3-interpolate';
import { format as d3_format } from 'd3-format';
import { isoParse as d3_isoParse } from 'd3-time-format';

import { PbdsDatavizService } from './dataviz.service';
import { PbdsDatavizPie } from './dataviz.interfaces';

@Component({
  selector: 'pbds-dataviz-pie',
  template: ``,
  styleUrls: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PbdsDatavizPieComponent implements OnInit, OnChanges, OnDestroy {
  @HostBinding('class.pbds-chart')
  chartClass = true;

  @HostBinding('class.pbds-chart-pie')
  pieClass = true;

  @Input()
  data: Array<PbdsDatavizPie>;

  @Input()
  width = 300;

  @Input()
  type: 'pie' | 'donut' = 'pie';

  @Input()
  monochrome = false;

  @Input()
  legendLabelFormatType: 'time' = null;

  @Input()
  legendLabelFormatString = '';

  @Input()
  legendValueFormatString = '';

  @Input()
  tooltipLabelFormatType: 'time' = null;

  @Input()
  tooltipLabelFormatString = '';

  @Input()
  tooltipValueFormatString = '';

  @Input()
  theme;

  @Output()
  hovered: EventEmitter<object> = new EventEmitter<object>();

  @Output()
  clicked: EventEmitter<object> = new EventEmitter<object>();

  private currentData = [];
  private height;
  private chart;
  private margin;
  private colors;
  private colorRange;
  private arc;
  private arcZoom;
  private svg;
  private pie;
  private legendLabelFormat;
  private legendValueFormat;
  private innerRadius;
  private anglePad;
  private outerRadius;
  private tooltip;
  private tooltipLabelFormat;
  private tooltipValueFormat;

  constructor(private _dataviz: PbdsDatavizService, private _element: ElementRef) {}

  ngOnInit() {
    this.margin = { top: 10, right: 10, bottom: 10, left: 10 };
    this.width = this.width - this.margin.left - this.margin.right;
    this.height = this.width - this.margin.top - this.margin.bottom;
    this.colors = this._dataviz.getColors(this.monochrome, this.theme);
    this.innerRadius = Math.min(this.width, this.height) / 2.5;
    this.outerRadius = Math.min(this.width, this.height) / 2;
    this.arcZoom = 10;
    this.anglePad = 0.02;
    this.legendValueFormat = d3_format(this.legendValueFormatString);
    this.tooltipValueFormat = d3_format(this.tooltipValueFormatString);

    // create formatters
    this.legendLabelFormat = this._dataviz.d3Format(this.legendLabelFormatType, this.legendLabelFormatString);
    this.tooltipLabelFormat = this._dataviz.d3Format(this.tooltipLabelFormatType, this.tooltipLabelFormatString);

    this.colorRange = d3_scaleOrdinal()
      .range(this.colors)
      .domain(this.data.map(c => c.label));

    if (this.type === 'pie') {
      this.innerRadius = 0;
      this.anglePad = 0;
    }

    this.pie = d3_pie()
      .padAngle(this.anglePad)
      .value((d: any) => d.value)
      .sort(null);

    this.arc = d3_arc()
      .padRadius(this.outerRadius)
      .innerRadius(this.innerRadius);

    this.chart = d3_select(this._element.nativeElement).attr('aria-hidden', 'true');

    this.svg = this.chart
      .append('svg')
      .attr('width', this.width)
      .attr('height', this.height)
      .attr('class', 'img-fluid')
      .attr('preserveAspectRatio', 'xMinYMin meet')
      .attr(
        'viewBox',
        `-${this.width / 2 + this.margin.left} -${this.height / 2 + this.margin.top} ${this.width +
          this.margin.left +
          this.margin.right} ${this.height + this.margin.top + this.margin.bottom}`
      );

    this.chart.append('ul').attr('class', 'legend legend-right');

    this.tooltip = this.chart
      .append('div')
      .style('opacity', 0)
      .attr('class', 'pbds-tooltip')
      .attr('aria-hidden', 'true');

    this.updateChart();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.data && !changes.data.firstChange) {
      this.updateChart(false);
    }
  }

  ngOnDestroy() {
    if (this.tooltip) this.tooltip.remove();
  }

  updateChart = (firstRun = true) => {
    // slices
    this.svg
      .selectAll('path')
      .data(this.pie(this.data))
      .join(
        enter => {
          const path = enter.append('path');

          path
            .each((d: any) => (d.outerRadius = this.outerRadius))
            .attr('fill', (d: any) => this.colorRange(d.data.label))
            .attr('class', 'slice')
            .each((d, i, nodes) => {
              this.currentData.splice(i, 1, d);
            });

          if (this.type === 'pie') {
            path
              .style('stroke', '#fff')
              .style('stroke-width', 2)
              .style('stroke-alignment', 'inner');
          }

          path.call(path =>
            path
              .transition()
              .duration((d, i, n) => (firstRun ? 0 : 500))
              .attrTween('d', this.arcEnterTween)
          );

          return path;
        },
        update => {
          this.tooltipHide();

          update
            .each((d: any) => (d.outerRadius = this.outerRadius))
            .call(update =>
              update
                .transition()
                .duration(500)
                .attrTween('d', this.arcTween)
            );
          return update;
        },
        exit => exit.remove()
      )
      .on('mouseover', (data, index, nodes) => {
        this.pathMouseOver(d3_event, data, index, nodes);
        // this.tooltipShow(this.chart.node(), data);
      })
      .on('mousemove', (data, index, nodes) => {
        this.tooltipShow(this.chart.node(), data);
        this.tooltipMove(this.chart.node());
      })
      .on('mouseout', (data, index, nodes) => {
        this.pathMouseOut(data, index, nodes);
        this.tooltipHide();
      })
      .on('click', (data, index, nodes) => {
        this.pathClick(d3_event, data, index, nodes);
      });

    // legend
    this.chart
      .select('.legend')
      .selectAll('.legend-item')
      .data(this.data)
      .join(
        enter => {
          const li = enter.append('li').attr('class', 'legend-item');

          li.append('span')
            .attr('class', 'legend-key')
            .style('background-color', (d: any) => this.colorRange(d.label));

          const description = li.append('span').attr('class', 'legend-description');

          description
            .append('span')
            .attr('class', 'legend-label')
            .html((d: any) => {
              switch (this.legendLabelFormatType) {
                case 'time':
                  const parsedTime = d3_isoParse(d.label);
                  return this.legendLabelFormat(parsedTime);

                default:
                  return d.label;
              }
            });

          description
            .append('span')
            .attr('class', 'legend-value')
            .html((d: any) => this.legendValueFormat(d.value));

          return li;
        },
        update => {
          update.selectAll('.legend-key').style('background-color', (d: any) => this.colorRange(d.label));

          update.select('.legend-label').html((d: any) => {
            switch (this.legendLabelFormatType) {
              case 'time':
                const parsedTime = d3_isoParse(d.label);
                return this.legendLabelFormat(parsedTime);

              default:
                return d.label;
            }
          });

          update.select('.legend-value').html((d: any) => this.legendValueFormat(d.value));

          return update;
        },
        exit => exit.remove()
      )

      .on('mouseover focus', (data, index, nodes) => {
        this.legendMouseOverFocus(data, index, nodes);
        this.pathMouseOver(d3_event, data, index, nodes);
      })
      .on('mouseout blur', (data, index, nodes) => {
        this.legendMouseOutBlur(data, index, nodes);
        this.pathMouseOut(data, index, nodes);
      })
      .on('click', (data, index, nodes) => {
        this.clicked.emit({ event: d3_event, data: data });
      });
  };

  private arcEnterTween = (data, index, nodes) => {
    const i = d3_interpolate(data.startAngle, data.endAngle);
    return t => {
      data.endAngle = i(t);
      return this.arc(data);
    };
  };

  private arcTween = (data, index, nodes) => {
    // console.log('ARGS: ', data, index, nodes);
    const i = d3_interpolate(this.currentData[index], data);

    this.currentData[index] = i(1);
    return t => this.arc(i(t));
  };

  private arcExitTween = (data, index, nodes) => {
    const end = Object.assign({}, this.currentData[index], { startAngle: this.currentData[index].endAngle });
    const i = d3_interpolate(data, end);

    return t => {
      return this.arc(i(t));
    };
  };

  private arcMouseOverTween = (data, index, nodes) => {
    const i = d3_interpolate(data.outerRadius, this.outerRadius + this.arcZoom);

    return t => {
      data.outerRadius = i(t);
      return this.arc(data);
    };
  };

  private arcMouseOutTween = (data, index, nodes) => {
    const i = d3_interpolate(data.outerRadius, this.outerRadius);
    return t => {
      data.outerRadius = i(t);
      return this.arc(data);
    };
  };

  private legendMouseOverFocus = (data, index, nodes) => {
    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);
  };

  private legendMouseOutBlur = (data, index, nodes) => {
    this.chart.selectAll('.legend-item').classed('inactive', false);
  };

  private pathMouseOver = (event, data, index, nodes) => {
    const slices = this.chart.selectAll('.slice');
    const slice = slices.filter((d, i) => i === index);

    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    slices.filter((d, i) => i !== index).classed('inactive', true);

    slice
      .transition()
      .duration(300)
      .delay(0)
      .attrTween('d', this.arcMouseOverTween);

    this.hovered.emit({
      event: event,
      data: data.data ? data.data : data // legend hover data is different than slice hover data
    });
  };

  private pathMouseOut = (data, index, value) => {
    const slices = this.chart.selectAll('.slice');
    const slice = slices.filter((d, i) => i === index);

    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', false);

    slices.classed('inactive', false);

    slice
      .transition()
      .duration(300)
      .delay(0)
      .attrTween('d', this.arcMouseOutTween);
  };

  private pathClick = (event, data, index, nodes) => {
    this.clicked.emit({
      event: event,
      data: data.data
    });
  };

  private tooltipShow = (node, data) => {
    this.tooltipSetPosition(node);

    const percentage = (data.endAngle - data.startAngle) / (2 * Math.PI);
    let label;

    switch (this.tooltipLabelFormatType) {
      case 'time':
        const parsedTime = d3_isoParse(data.data.label);
        label = this.tooltipLabelFormat(parsedTime);
        break;

      default:
        label = data.data.label;
    }

    this.tooltip.html(
      `
        <div class="tooltip-label">${label}</div>
        <div class="tooltip-value">${this.tooltipValueFormat(percentage)}</div>
      `
    );

    this.tooltip.style('opacity', 1);
  };

  private tooltipMove = node => {
    this.tooltipSetPosition(node);
  };

  private tooltipHide = () => {
    this.tooltip.style('opacity', 0);
  };

  private tooltipSetPosition = node => {
    const coordinates = d3_mouse(node);

    this.tooltip.style('left', `${coordinates[0] + 16}px`);
    this.tooltip.style('top', `${coordinates[1] + 16}px`);
  };
}
